/* ============== Scroll Button =============== */
.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 8rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* ================== Tablet Media queries ================== */
@media screen and (max-width: 1024px) {
    header {
        height: fit-content;
    }
}

/* ================== Mobile Media queries ================== */
@media screen and (max-width: 600px){
    header {
        height: fit-content;
    }

    .header_socials,
    .scroll_down {
        display: none;
    }
}
